//   //
import React from 'react';
import styled from 'styled-components';
import {useNavigate } from 'react-router-dom';
import '../components/Fonts.css';
import { useEffect, useState } from 'react';

const Container = styled.div`
  text-align: center;
  max-width: 100%;
  margin: 17rem auto 0;
`;

const Title = styled.h1`
  font-size: 2rem;
  margin-bottom: 2rem;
  font-weight: 700;
`;

const Descrip = styled.p`
  font-size: 1.2rem;
  margin-bottom: 2rem;
  line-height: 1.5;
  font-weight: 500;
`;

const StartButton = styled.button`
  font-size: 1.2rem;
  padding: 1rem 2rem;
  color: #fff;
  background-color: #004EE5;
  border: none;
  border-radius: 0.5rem;
  cursor: pointer;
  transition: background-color 0.3s;
  margin-bottom: 16rem;
  font-weight: 500;

  &:hover {
    background-color: #0056b3;
  }
`;

const Footer = styled.footer`
  background: #e9e8e8;
  margin-top: 2rem;
  font-size: 0.875rem;
  color: #888;
  padding: 1rem 0;

  p {
    margin: 0.5rem 0;
  }
`;

//        
const Homemain = () => {
    const navigate = useNavigate();
    const [isLoggedIn, setIsLoggedIn] = useState(false);

    useEffect(() => {
      const checkLoginStatus = () => {
        const token = localStorage.getItem('access_token'); 
        if (token) {
          setIsLoggedIn(true);
        }
      };

      checkLoginStatus();
    }, []);
  
    const handleStartClick = () => {
      if (isLoggedIn) {
        navigate('/mypage');
      } else {
        navigate('/login');
      }
    };
    
    return (
        <Container>
        <Title>     WITH</Title>
        <Descrip>
                    . <br />
             ,  ,   ,      <br />     .
        </Descrip>
        <StartButton onClick={handleStartClick}> </StartButton>
        <Footer>
            <p>TEL 02-6353-0200 (  1  4 ) |  Email inha@naver.com</p>
            <p>Copyright 2024 Reverse at Inha lionz.</p>
        </Footer>
        </Container>
    );
}

export default Homemain;
