import React from 'react';
import styled from 'styled-components';
import { useNavigate } from 'react-router-dom';

const Modalbackground = styled.div`
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
`;

const Modal = styled.div`
  background: white;
  padding: 30px;
  border-radius: 10px;
  max-width: 500px;
  text-align: center;
`;
const Scoretitle = styled.div`
  font-size: 25px;
  margin-bottom: 20px;
`;
const Score = styled.div`
  font-size: 24px;
  margin-bottom: 20px;
  font-weight: bold;
  color: ${props => {
    if (props.score >= 70) return 'red';
    if (props.score >= 40) return 'orange';
    if (props.score <= 30) return 'green';
    return 'black';
  }};
`;

const Message = styled.p`
  margin-bottom: 20px;
  line-height: 1.8;
  color: ${props => {
    if (props.score >= 70) return 'red';
    if (props.score >= 40) return 'orange';
    if (props.score <= 30) return 'green';
    return 'black';
  }};
`;

const Linkbutton = styled.a`
  display: inline-block;
  margin: 10px;
  padding: 10px 20px;
  background-color: #004EE5;
  color: white;
  text-decoration: none;
  border-radius: 5px;
  cursor: pointer;
`;

const Footer = styled.div`
    margin-top: 10px;
    font-size: 12px;
    color: gray;
`;

const ReResultModal = ({ score }) => {
    const getRiskMessage = () => {
        if (score >= 70) return '   .';
        if (score >= 40) return '   .';
        if (score <= 30) return '   .';
        return '   .';
      };

    const Link = styled.a`
        color: #004EE5;
        cursor: pointer;
        text-decoration: none;
    `;
    const navigate = useNavigate();
    const handletextclick = () => {
        navigate('/retest'); 
    };


  return (
    <Modalbackground>
      <Modal>
        <Scoretitle>  ? </Scoretitle>
        <Score score={score}>{score}</Score>
        <Message>
           , {getRiskMessage()} <br/>
             /   . <br/>
            ? <Link onClick={handletextclick}>  </Link>

        </Message>
        <Linkbutton href="/record">  </Linkbutton>
        <Footer>:    (02-6353-0200)</Footer>
      </Modal>
    </Modalbackground>
  );
};

export default ReResultModal;
