import React, { useState, useEffect, useRef } from "react";
import styled, { createGlobalStyle } from "styled-components";
import { useNavigate } from "react-router-dom";
import ReactQuill from "react-quill";
import "react-quill/dist/quill.snow.css";
import axiosInstance from "../axiosInstance";
import Modal from "react-modal";
import "react-calendar/dist/Calendar.css";
import Sidebar from "./Sidebar"; 

const GlobalStyle = createGlobalStyle`
  body {
    overflow: ${({ isDimmed }) => (isDimmed ? "hidden" : "auto")};
  }
`;

const Container = styled.div`
  max-width: 75rem;
  margin: auto;
  margin-top: 2.5rem;
`;

const SubContainer = styled.div`
  display: flex;
  gap: 3.75rem;
`;

const Content = styled.div`
  flex: 2;
  padding: 0 0 0 1.25rem;
  display: flex;
  flex-direction: column;
`;

const PostContainer = styled.div`
  flex: 1;
  padding: 0 0 0 1.25rem;
`;

const Form = styled.form`
  display: flex;
  flex-direction: column;
`;

const Label = styled.label`
  margin-bottom: 0.3125rem;
  font-weight: bold;
  display: ${({ hidden }) => (hidden ? "none" : "block")}; // hidden prop  
`;

const Input = styled.input`
  padding: 0.625rem;
  margin-bottom: 1.25rem;
  border: 1px solid #ddd;
  border-radius: 0.3125rem;
`;

const QuillWrapper = styled.div`
  .ql-editor {
    min-height: 36rem;
  }
  margin-bottom: 0.25rem;
`;

const ButtonContainer = styled.div`
  display: flex;
  justify-content: flex-end;
  margin-bottom: 1.25rem;
  gap: 0.625rem;
`;

const Button = styled.button`
  cursor: pointer;
  background-color: #004ee5;
  color: #fff;
  padding: 0.75rem 1.5rem;
  margin-top: 0.75rem;
  border: 1px solid #b0b0b0;
  border-radius: 12px;
  &:hover {
    background-color: #1f66b3;
  }
`;

const CancelButton = styled(Button)`
  background-color: #a2a2a2;
  &:hover {
    background-color: #e2e2e2;
  }
`;

const SubmitButton = styled(Button)`
  background-color: #004ee5;
  &:hover {
    background-color: #2165ae;
  }
`;

const EditButton = styled(Button)`
  background-color: ${({ isEditing }) =>
    isEditing ? "#28a745" : "#004ee5"};
  margin-left: 0.5rem;

  &:hover {
    background-color: ${({ isEditing }) =>
      isEditing ? "#218838" : "#1f66b3"};
  }
`;

const DeleteButton = styled(Button)`
  background-color: #a2a2a2;
  margin-left: 0.5rem;

  &:hover {
    background-color: #e2e2e2;
  }
`;

const customStyles = {
  content: {
    top: "50%",
    left: "50%",
    right: "auto",
    bottom: "auto",
    marginRight: "-50%",
    transform: "translate(-50%, -50%)",
    width: "500px",
    padding: "20px",
    textAlign: "center",
  },
};

const Separator = styled.div`
  border-bottom: 1px solid #567191;
  width: 90%;
  margin: 1rem 0;
  align-self: center;
`;


const modules = {
  toolbar: [
    [{ header: "1" }, { header: "2" }, { font: [] }],
    [{ size: [] }],
    ["bold", "italic", "underline", "strike", "blockquote"],
    [{ list: "ordered" }, { list: "bullet" }, { indent: "-1" }, { indent: "+1" }],
    ["link", "image", "video"],
    ["clean"],
  ],
};

const formats = [
  "header",
  "font",
  "size",
  "bold",
  "italic",
  "underline",
  "strike",
  "blockquote",
  "list",
  "bullet",
  "indent",
  "link",
  "image",
  "video",
];

const Record = () => {
  const [title, setTitle] = useState(""); //   
  const [content, setContent] = useState(""); //   
  const [isDialogVisible, setIsDialogVisible] = useState(false); //   
  const [isSuccess, setIsSuccess] = useState(null); //  
  const [loginPromptIsOpen, setLoginPromptIsOpen] = useState(false); //    
  const [currentDate, setCurrentDate] = useState(new Date()); //  
  const [isEditing, setIsEditing] = useState(false); //   
  const [diaryExists, setDiaryExists] = useState(false); //   
  const [dialogType, setDialogType] = useState(null); //  
  const [diaryWritten, setDiaryWritten] = useState(false); //    
  const [helpModalIsOpen, setHelpModalIsOpen] = useState(false); //   
  const quillRef = useRef(null); // Quill  

  useEffect(() => {
    //   
    const token = localStorage.getItem("access_token");
    if (!token) {
      setLoginPromptIsOpen(true);
    }
  }, []);

  const fetchDiary = async (date) => {
    //     
    try {
      const formattedDate = date.toISOString().split("T")[0];
      const response = await axiosInstance.get(
        `/with/calendar/goal_diary/?date=${formattedDate}`
      );
      const { diary_entries } = response.data;

      if (diary_entries && diary_entries.length > 0) {
        const diary = diary_entries[0];
        setTitle(diary.title);
        setContent(diary.content);
        setDiaryExists(true); //   
        setDiaryWritten(true); //   
      } else {
        setTitle("");
        setContent("");
        setDiaryExists(false); //   
        setDiaryWritten(false); //    
      }
    } catch (error) {
      console.error("Failed to fetch diary:", error);
    }
  };

  useEffect(() => {
    fetchDiary(currentDate); //    
  }, [currentDate]);

  const handleDateChange = (date) => {
    //     
    setCurrentDate(date);
    fetchDiary(date);
  };

  const handleSubmit = (e) => {
    //  
    e.preventDefault();
    setDialogType("submit");
    setIsDialogVisible(true);
  };

  const handleSubmitConfirm = async () => {
    if (!title || !content) {
      console.error("   .");
      setIsSuccess(false);
      return;
    }

    const formattedDate = currentDate.toISOString().split("T")[0]; //  
    console.log(`Submitting diary entry: ${formattedDate}, ${title}, ${content}`); //  

    try {
      const response = await axiosInstance.post(
        "/with/calendar/diary/create/",
        {
          day: formattedDate,
          title: title,
          content: content,
        }
      );

      if (response.status === 201) {
        setIsSuccess(true);
        console.log("  :", response.data);
        setIsDialogVisible(false); //  
        setDiaryExists(true); //    
        setIsEditing(false); //   
        setDiaryWritten(true); //   
        fetchDiary(currentDate); // Immediately update the diary status
      } else {
        setIsSuccess(false);
        console.error("  ");
      }
    } catch (error) {
      setIsSuccess(false);
      console.error("  :", error.response?.data || error.message);
    }
  };

  const handleDialogCancel = () => {
    //   
    setDialogType(null);
    setIsDialogVisible(false);
  };

  const handleReset = () => {
    setTitle("");
    setContent("");
  };

  const handleEdit = () => {
    //   
    setIsEditing(true);
  };

  const handleEditConfirm = async () => {
    setIsEditing(false);

    if (!title || !content) {
      console.error("   .");
      setIsSuccess(false);
      return;
    }

    const formattedDate = currentDate.toISOString().split("T")[0]; //  
    console.log(`Updating diary entry: ${formattedDate}, ${title}, ${content}`); //  

    try {
      const response = await axiosInstance.post("/with/calendar/diary/update/", {
        day: formattedDate, //   
        title: title,
        content: content,
      });

      if (response.status === 200) {
        setIsSuccess(true);
        console.log("  :", response.data);
        setDiaryWritten(true); //   
        fetchDiary(currentDate);
      } else {
        setIsSuccess(false);
        console.error("  ");
      }
    } catch (error) {
      setIsSuccess(false);
      console.error("  :", error.response?.data || error.message);
    }
  };

  const handleDelete = () => {
    //   
    setDialogType("delete");
    setIsDialogVisible(true);
  };

  const handleDeleteConfirm = async () => {
    const formattedDate = currentDate.toISOString().split("T")[0]; //  
    console.log(`Deleting diary entry: ${formattedDate}`); //  

    try {
      const response = await axiosInstance.delete(
        "/with/calendar/diary/delete/",
        {
          data: {
            day: formattedDate, //  
          },
        }
      );

      if (response.status === 204) {
        setIsSuccess(true);
        console.log("  ");
        setTitle("");
        setContent("");
        setDiaryExists(false); //   
        setDiaryWritten(false); //  
        setIsDialogVisible(false); //  
        fetchDiary(currentDate);
      } else {
        setIsSuccess(false);
        console.error("  ");
      }
    } catch (error) {
      setIsSuccess(false);
      console.error("  :", error.response?.data || error.message);
    }
  };

  const fetchSurveyScore = async () => {
    // Fetch survey score
    try {
      const response = await axiosInstance.get("/with/score/");

      if (response.status === 200) {
        const { survey_score } = response.data;
        console.log("Survey score fetched successfully:", survey_score);
        // Update UI or state with the survey score as needed
      } else {
        console.error("Failed to fetch survey score");
      }
    } catch (error) {
      console.error("Survey score fetch error:", error.response?.data || error.message);
    }
  };

  const handleLoginConfirm = () => {
    setLoginPromptIsOpen(false);
    window.location.href = "/login";
  };

  useEffect(() => {
    fetchSurveyScore();
  }, []);

  return (
    <>
      <GlobalStyle isDimmed={isDialogVisible || loginPromptIsOpen || helpModalIsOpen} />
      <Container>
        <SubContainer>
          <Sidebar 
            onDateChange={handleDateChange} 
            diaryWritten={diaryWritten}
          />
          <Content>
            <PostContainer>
              <div
                style={{
                  display: "flex",
                  justifyContent: "space-between",
                  alignItems: "center",
                }}
              >
                <h2> </h2>
                {diaryExists && (
                  <div>
                    <EditButton
                      isEditing={isEditing}
                      onClick={isEditing ? handleEditConfirm : handleEdit}
                    >
                      {isEditing ? "" : ""}
                    </EditButton>
                    <DeleteButton onClick={handleDelete}></DeleteButton>
                  </div>
                )}
              </div>
              <Form onSubmit={handleSubmit}>
                <Label htmlFor="title" hidden={diaryExists && !isEditing}>
                    .
                </Label>
                <Input
                  type="text"
                  id="title"
                  value={title}
                  onChange={(e) => setTitle(e.target.value)}
                  required
                  disabled={!isEditing && diaryExists}
                />
                <Label htmlFor="content" hidden={diaryExists && !isEditing}>
                    .
                </Label>
                <QuillWrapper>
                  <ReactQuill
                    ref={quillRef}
                    value={content}
                    onChange={setContent}
                    modules={modules}
                    formats={formats}
                    readOnly={!isEditing && diaryExists}
                  />
                </QuillWrapper>
                {!diaryExists && (
                  <ButtonContainer>
                    <CancelButton type="button" onClick={handleReset}>
                      
                    </CancelButton>
                    <SubmitButton type="submit"></SubmitButton>
                  </ButtonContainer>
                )}
              </Form>
            </PostContainer>
          </Content>
        </SubContainer>
      </Container>

      {isDialogVisible && (
        <Modal
          isOpen={isDialogVisible}
          onRequestClose={handleDialogCancel}
          style={customStyles}
          contentLabel="Confirmation"
          ariaHideApp={false}
        >
          <h2>{dialogType === "submit" ? " " : " "}</h2>
          <Separator />
          <p>
            {dialogType === "submit"
              ? " ?"
              : "  ?"}
          </p>
          <div>
            <Button
              onClick={
                dialogType === "submit"
                  ? handleSubmitConfirm
                  : handleDeleteConfirm
              }
            >
              {dialogType === "submit" ? "" : ""}
            </Button>
            <Button onClick={handleDialogCancel}></Button>
          </div>
        </Modal>
      )}

      {loginPromptIsOpen && (
        <Modal
          isOpen={loginPromptIsOpen}
          onRequestClose={() => setLoginPromptIsOpen(false)}
          style={customStyles}
          contentLabel="Login Prompt"
          ariaHideApp={false}
        >
          <h2> </h2>
          <Separator />
          <p>   .</p>
          <Button onClick={handleLoginConfirm}></Button>
        </Modal>
      )}
    </>
  );
};

export default Record;