import React, { useState } from 'react';
import styled from 'styled-components';
import Question from './Question';
import Resultmodal from './Resultmodal';
import axiosInstance from '../axiosInstance';
import { useEffect } from 'react';
import {jwtDecode} from 'jwt-decode';

//axios  

const Container = styled.div`
  padding: 20px;
  max-width: 800px;
  margin: 0 auto;
  text-align: center;
`;

const Title = styled.h1`
  margin-bottom: 10px;
`;

const Descrip = styled.p`
  margin-bottom: 30px;
  line-height: 1.5;
  color: gray;
  font-size: 16px;
`;

const Qlist = styled.div`
  border: 1px solid #dadada;
  border-radius: 10px;
  background-color: #f4f4f4;
  padding: 20px;
  margin-bottom: 20px;
  text-align: left;
`;

const Category = styled.p`
  margin-top: 20px;
  margin-bottom: 10px;
  margin-left: 0.6rem;
  font-size: 17px;
  font-weight: bold;
  color: #333;
`;

const Qstyle = styled.div`
  border: 1px solid #ddd;
  border-radius: 10px;
  background-color: #ffffff;
  margin-left: 1rem;
  margin-right: 1rem;
  margin-bottom: 0.5rem;
  height: 2.8rem;
`;

const Submit = styled.button`
  padding: 15px 30px;
  font-size: 16px;
  background-color: #004EE5;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
`;

const FooterText = styled.p`
  margin-top: 20px;
  color: gray;
  font-size: 10px;
  text-align: left;
`;

const BoldText = styled.span`
  font-weight: bold;
`;

const Signtest = () => {
  const questions = [
    " 10  (, , , , )    ? (2  )",
    " 1    ? (1  )",
    " 1   ? (1  )",
    " 1    (Blackout)    ? (1  )",
    "        ? ( )",
    "       ? ( )",
    "        ? ( )",
    " 10  10       ( )",
    "   ()      ( )",
    "    (, , , , , )"
  ];

  // false 
  const [selectedCount, setSelectedCount] = useState(Array(questions.length).fill(false));

  const [showResult, setShowResult] = useState(false);
  const [score, setScore] = useState(0);
  const [user, setUser] = useState('');

  useEffect(() => {
    const token = localStorage.getItem('access_token');
    if (token) {
      try {
        const decodedToken = jwtDecode(token);
        const currentTime = Date.now() / 1000;
        if (decodedToken.exp < currentTime) {
          throw new Error('Token expired');
        }
        setUser(decodedToken.user_id);
     } catch (error) {
        console.error('Invalid token:', error);
        alert('  .   .');
      }
    }
  }, []);

  useEffect(() => {
    console.log('user:', user);
  }, [user]);

  //  True, False 
  const handleCheckboxChange = (index) => {
    const newSelectedCount = [...selectedCount];
    newSelectedCount[index] = !newSelectedCount[index];
    setSelectedCount(newSelectedCount);
  };

  const calculateScore = async () => {
    const totalScore = selectedCount.filter(selected => selected).length * 10;
    setScore(totalScore);
    setShowResult(true);

    //T/F  surveyData 
    const answers = selectedCount.map(selected => selected);

    const surveyData = {
      user: user,
      answer1: answers[0],
      answer2: answers[1],
      answer3: answers[2],
      answer4: answers[3],
      answer5: answers[4],
      answer6: answers[5],
      answer7: answers[6],
      answer8: answers[7],
      answer9: answers[8],
      answer10: answers[9],
      score: totalScore
    };
    //post data 
    console.log('Survey Data:', surveyData);

    //
    try {
      const response = await axiosInstance.post('/with/signup/survey/', surveyData);
      console.log('API Response:', response);
      if (response.status !== 200 && response.status !== 201) {
        throw new Error('Network response was not ok');
      }
      console.log('Survey submitted successfully:', response.data);
    } catch (error) {
      console.error(' :', error.response || error);
      if (error.response && error.response.data) {
        console.error('Error details:', error.response.data);
      }
      alert('  .  .');
    }
  };

  return (
    <Container>
      <Title>  </Title>
      <Descrip>
            ! <br />
            . <br />
                .
      </Descrip>
      <Qlist>
        <Category>,   (1)</Category>
        <Qstyle>
          <Question
          question={questions[0]}
          index={0}
          onChange={handleCheckboxChange}
        />
        </Qstyle>
        
        
        <Category>  (3)</Category>
        <Qstyle>
          <Question
          question={questions[1]}
          index={1}
          onChange={handleCheckboxChange}
        />
        </Qstyle>
        <Qstyle>
          <Question
          question={questions[2]}
          index={2}
          onChange={handleCheckboxChange}
        />
        </Qstyle>
        <Qstyle>
          <Question
          question={questions[3]}
          index={3}
          onChange={handleCheckboxChange}
        />
        </Qstyle>
        
        <Category>  (3)</Category>
        <Qstyle>
          <Question
          question={questions[4]}
          index={4}
          onChange={handleCheckboxChange}
        />
        </Qstyle>
        <Qstyle>
          <Question
          question={questions[5]}
          index={5}
          onChange={handleCheckboxChange}
        />
        </Qstyle>
        <Qstyle>
          <Question
          question={questions[6]}
          index={6}
          onChange={handleCheckboxChange}
        />
        </Qstyle>
        
        <Category>   (1)</Category>
        <Qstyle>
          <Question
          question={questions[7]}
          index={7}
          onChange={handleCheckboxChange}
        />
        </Qstyle>
        
        <Category>  (2)</Category>
        <Qstyle>
          <Question
          question={questions[8]}
          index={8}
          onChange={handleCheckboxChange}
        />
        </Qstyle>
        <Qstyle>
          <Question
          question={questions[9]}
          index={9}
          onChange={handleCheckboxChange}
        />
        </Qstyle>
        
      </Qlist>
      <FooterText>
           ""  ""   .         .        ,       . 
        <BoldText>      .</BoldText>
        <br />
        <br />
        * : (2022),   
      </FooterText>
      <Submit onClick={calculateScore}>  </Submit>
      {showResult && <Resultmodal score={score} />}
    </Container>
  );
};

export default Signtest;