import React from 'react';
import styled from 'styled-components';
import RetestModal from './RetestModal'; 
import { useState } from 'react';

const Container = styled.div`
  padding: 20px;
  max-width: 800px;
  margin: 0 auto;
  text-align: center;
`;

const Title = styled.h1`
  text-align: center;
`;

const Subtitle = styled.p`
  text-align: center;
  color: #adacac;
  margin-bottom: 1.6rem
`;

const Qlist = styled.div`
  border: 1px solid #dadada;
  border-radius: 10px;
  background-color: #f4f4f4;
  padding: 15px;
  margin-bottom: 20px;
  text-align: left;
`;

const Category = styled.h2`
  margin-top: 1rem;
  margin-bottom: 10px;
  margin-left: 0.6rem;
  font-size: 18px;
  font-weight: bold;
  color: black;
`;

const Qstyle = styled.div`
  border: 1px solid #ddd;
  border-radius: 10px;
  background-color: #ffffff;
  margin-left: 1rem;
  margin-right: 1rem;
  margin-bottom: 0.5rem;
  height: 22rem;
`;

const Label = styled.label`
  display: block;
  margin: 10px 0 5px;
  margin-left: 1.5rem;
  font-size: 15px;
  font-weight: bold;
`;

const Line = styled.div`
  position: flex;
  width: 40rem;
  height: 0.1rem;
  background: #747474;
`;

const List = styled.ul`
  list-style-type: none;
  margin-left: 30rem;
  padding: 0;
`;

const ListItem = styled.li`
  margin: 5px 0;
  font-size: 14px;
`;

const RadioInput = styled.input`
  margin-right: 10px;
`;

const ErrorMessage = styled.p`
  color: red;
  font-size: 12px;
  margin-top: 3.5rem;
`;

const SubmitButton = styled.button`
  padding: 15px 30px;
  font-size: 16px;
  background-color: #004EE5;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  margin-bottom: 3rem;
  margin-top: 3rem;

  &:hover {
    background-color: #0056b3;
  }
`;

const Retest = () => {
  const [answers, setAnswers] = useState({
    loneliness1: null,
    loneliness2: null,
    support1: null,
    support2: null,
    network1: null,
    network2: null,
  });
  const [showModal, setShowModal] = useState(false);
  const [result, setResult] = useState('');
  const [error, setError] = useState('');

  const handleChange = (e) => {
    setAnswers({
      ...answers,
      [e.target.name]: parseInt(e.target.value, 10),
    });
  };

  const handleSubmit = () => {
    if (Object.values(answers).includes(null)) {
      setError('    ');
      return;
    }

    const lonelinessScore = answers.loneliness1 + answers.loneliness2;
    const supportScore = answers.support1 + answers.support2;
    const networkScore = answers.network1 + answers.network2;

    let resultText = '';
    if (lonelinessScore >= 3 && (supportScore >= 4 || networkScore >= 4)) {
      resultText = ' ,  ';
    } else if (lonelinessScore >= 3) {
      resultText = ' ';
    } else if (supportScore >= 4 || networkScore >= 4) {
      resultText = ' ';
    }

    setResult(resultText);
    setShowModal(true);
    setError('');
  };

  return (
    <Container>
      <Title>    </Title>
      <Subtitle>      </Subtitle>
      
      <Qlist>
        <Category> </Category>
        <Qstyle>
          <br />
          <Label> </Label>
          <List>
            <ListItem><RadioInput type="radio" name="loneliness1" value="0" onChange={handleChange}/>    (0)</ListItem>
            <ListItem><RadioInput type="radio" name="loneliness1" value="1" onChange={handleChange}/>    (1)</ListItem>
            <ListItem><RadioInput type="radio" name="loneliness1" value="2" onChange={handleChange}/>   (2)</ListItem>
            <ListItem><RadioInput type="radio" name="loneliness1" value="3" onChange={handleChange}/>   (3)</ListItem>
          </List>
          <Line />
          <br />
          <Label>  </Label>
          <List>
            <ListItem><RadioInput type="radio" name="loneliness2" value="0" onChange={handleChange}/>    (0)</ListItem>
            <ListItem><RadioInput type="radio" name="loneliness2" value="1" onChange={handleChange}/>    (1)</ListItem>
            <ListItem><RadioInput type="radio" name="loneliness2" value="2" onChange={handleChange}/>   (2)</ListItem>
            <ListItem><RadioInput type="radio" name="loneliness2" value="3" onChange={handleChange}/>   (3)</ListItem>
          </List>
        </Qstyle>
        
        <Category>  </Category>
        <Qstyle>
          <br />
          <Label>     </Label>
          <List>
            <ListItem><RadioInput type="radio" name="support1" value="3" onChange={handleChange}/>    (3)</ListItem>
            <ListItem><RadioInput type="radio" name="support1" value="2" onChange={handleChange}/>    (2)</ListItem>
            <ListItem><RadioInput type="radio" name="support1" value="1" onChange={handleChange}/>   (1)</ListItem>
            <ListItem><RadioInput type="radio" name="support1" value="0" onChange={handleChange}/>   (0)</ListItem>
          </List>
          <Line />
          <br />
          <Label>     </Label>
          <List>
            <ListItem><RadioInput type="radio" name="support2" value="3" onChange={handleChange}/>    (3)</ListItem>
            <ListItem><RadioInput type="radio" name="support2" value="2" onChange={handleChange}/>    (2)</ListItem>
            <ListItem><RadioInput type="radio" name="support2" value="1" onChange={handleChange}/>   (1)</ListItem>
            <ListItem><RadioInput type="radio" name="support2" value="0" onChange={handleChange}/>   (0)</ListItem>
          </List>
        </Qstyle>
        
        <Category>  </Category>
        <Qstyle> 
          <br />
          <Label> 1   ,  1   ?</Label>
          <List>
            <ListItem><RadioInput type="radio" name="network1" value="3" onChange={handleChange}/> 0 (3)</ListItem>
            <ListItem><RadioInput type="radio" name="network1" value="2" onChange={handleChange}/> 1 ~ 2 (2)</ListItem>
            <ListItem><RadioInput type="radio" name="network1" value="1" onChange={handleChange}/> 3 ~ 6 (1)</ListItem>
            <ListItem><RadioInput type="radio" name="network1" value="0" onChange={handleChange}/> 7  (0)</ListItem>
          </List>
          <Line />
          <br />
          <Label>         ?</Label>
          <List>
            <ListItem><RadioInput type="radio" name="network2" value="3" onChange={handleChange}/>    (3)</ListItem>
            <ListItem><RadioInput type="radio" name="network2" value="2" onChange={handleChange}/> 15  (2)</ListItem>
            <ListItem><RadioInput type="radio" name="network2" value="1" onChange={handleChange}/> 15 ~ 1 (1)</ListItem>
            <ListItem><RadioInput type="radio" name="network2" value="0" onChange={handleChange}/> 1  (0)</ListItem>
          </List>
        </Qstyle>
      </Qlist>
      {error && <ErrorMessage>{error}</ErrorMessage>}
      <SubmitButton onClick={handleSubmit}> </SubmitButton>
      {showModal && <RetestModal result={result} onClose={() => setShowModal(false)} />}
    </Container>
  );
};

export default Retest;
