import React, { useState } from 'react';
import styled from 'styled-components';
import { useNavigate } from 'react-router-dom'; // useNavigate  
import axiosInstance from '../axiosInstance'; // axiosInstance   URL  

//   
const Container = styled.div`
  max-width: 600px;
  margin: 2rem auto;
  padding: 2rem;
  background-color: #fff;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  border-radius: 8px;
`;

const Title = styled.h2`
  text-align: center;
  margin-bottom: 1.5rem;
`;

const Form = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
`;

const Input = styled.input`
  margin: 0.5rem 0;
  padding: 0.5rem;
  width: 100%;
  border: 1px solid #c6c6c6;
  border-radius: 4px;
  box-sizing: border-box;
`;

const TextArea = styled.textarea`
  margin: 0.5rem 0;
  padding: 0.5rem;
  width: 100%;
  height: 100px;
  border: 1px solid #c6c6c6;
  border-radius: 4px;
  box-sizing: border-box;
`;

const ButtonContainer = styled.div`
  display: flex;
  justify-content: center;
  gap: 0.2rem;
  margin-top: 1rem;
`;

const Button = styled.button`
  cursor: pointer;
  background-color: #004EE5;
  color: #fff;
  padding: 0.75rem 1.5rem;
  border: none;
  border-radius: 12px;
  &:hover {
    background-color: #0040c1;
  }
`;

const CancelButton = styled.button`
  cursor: pointer;
  background-color: #004EE5;
  color: #fff;
  padding: 0.75rem 1.5rem;
  border: none;
  border-radius: 12px;
  &:hover {
    background-color: #0040c1;
  }
`;

const Message = styled.p`
  margin-top: 1rem;
  color: ${({ isError }) => (isError ? 'red' : 'green')};
  font-weight: bold;
  text-align: center;
`;

const Separator = styled.div`
  border-bottom: 1px solid #567191;
  width: 100%;
  margin: 1rem 0;
`;

const WarningText = styled.p`
  color: #888888;
  font-size: 0.8rem;
  text-align: center;
`;

const ConsultationApplication = () => {
  const [form, setForm] = useState({
    available_time: '',
    reason: '',
    phone_number: '',
  });

  const [successMessage, setSuccessMessage] = useState('');
  const [isError, setIsError] = useState(false);
  const [isSubmitted, setIsSubmitted] = useState(false);

  const navigate = useNavigate(); // useNavigate  

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setForm((prevForm) => ({
      ...prevForm,
      [name]: value,
    }));
  };

  const handleSubmit = async () => {
    const { available_time, reason, phone_number } = form;

    //   
    if (!available_time || !reason || !phone_number) {
      setSuccessMessage('  .');
      setIsError(true);
      return;
    }

    const dataToSend = { available_time, reason, phone_number };

    try {
      setSuccessMessage('  ...');

      //      
      const token = localStorage.getItem('access_token');
      if (!token) {
        setSuccessMessage(' .');
        setIsError(true);
        //   
        window.location.href = '/login';
        return;
      }

      //  :   API 
      const response = await axiosInstance.post('/with/consulting/application/', dataToSend);

      console.log('Form submitted successfully:', response.data);

      setSuccessMessage(' .     .');
      setIsError(false);
      setForm({
        available_time: '',
        reason: '',
        phone_number: '',
      });
      setIsSubmitted(true);
    } catch (error) {
      console.error('   :', error);
      
      if (error.response && error.response.status === 401) {
        //   
        setSuccessMessage('  .  .');
        setIsError(true);
        //   
        window.location.href = '/login';
      } else {
        setSuccessMessage('   .  .');
        setIsError(true);
      }
    }
  };

  const handleCancel = () => {
    navigate(-1); //   
  };

  const handleNewConsultation = () => {
    setIsSubmitted(false);
    setSuccessMessage('');
    navigate('/consulting');
  };

  const handleGoToMypage = () => {
    navigate('/mypage');
  };

  return (
    <Container>
      <Title> </Title>
      <Form>
        {!isSubmitted ? (
          <>
            <Input
              type="tel"
              name="phone_number"
              placeholder=""
              value={form.phone_number}
              onChange={handleInputChange}
            />
            <Input
              type="datetime-local"
              name="available_time"
              placeholder=" "
              value={form.available_time}
              onChange={handleInputChange}
            />
            <TextArea
              name="reason"
              placeholder=" "
              value={form.reason}
              onChange={handleInputChange}
            />
            <WarningText>
              /         . <br />
                      .
            </WarningText>
            <ButtonContainer>
              <Button onClick={handleSubmit}></Button>
              <CancelButton onClick={handleCancel}></CancelButton>
            </ButtonContainer>
          </>
        ) : (
          <>
            <Separator />
            <Message isError={isError}>{successMessage}</Message>
            <ButtonContainer>
              <Button onClick={handleNewConsultation}>   </Button>
              <Button onClick={handleGoToMypage}> </Button>
            </ButtonContainer>
          </>
        )}
      </Form>
    </Container>
  );
};

export default ConsultationApplication;
