import React, { useState } from 'react';
import styled from 'styled-components';
import { useNavigate } from 'react-router-dom';
import SearchSide from './SearchSide';
import Modal from 'react-modal';
import '../../components/Fonts.css';
import Consul1Image from '../../img/consul1.png';
import Consul4Image from '../../img/consul4.png';
import Consul5Image from '../../img/consul5.png';
import Consul6Image from '../../img/consul6.png';
import MessageIconImage from '../../img/message.png';
import CallIconImage from '../../img/call.png';
import LocationIconImage from '../../img/location.png';

const ConsultingContainer = styled.div`
  display: flex;
  background-color: #fff;
  padding: 2rem 10rem;
`;

const ResultSide = styled.div`
  flex: 1;
  padding: 1rem;
`;

const ResultHeader = styled.h2`
  margin-bottom: 1rem;
`;

const ResultBorder = styled.div`
  border-bottom: 1px solid #000;
  margin-bottom: 1rem;
`;

const Result = styled.div`
  display: flex;
  flex-direction: column;
`;

const ResultItem = styled.div`
  display: flex;
  flex-direction: column;
  padding: 1rem;
  background-color: #fff;
  margin-bottom: 1rem;
  border: 1px solid #c6c6c6;
`;

const ResultContent = styled.div`
  display: flex;
  margin-bottom: 1rem;
`;

const ResultImage = styled.img`
  width: 6rem;
  height: 6rem;
  border-radius: 50%;
  margin-right: 1rem;
`;

const ResultDetails = styled.div`
  flex: 1;
`;

const ResultTitle = styled.h3`
  margin: 0;
  font-size: 1.6rem;
`;

const Highlight = styled.span`
  color: #0062dc;
`;

const ResultDescription = styled.p`
  color: #7d7d7d;
  margin: 0.5rem 0 0 0;
`;

const Prices = styled.div`
  display: flex;
  justify-content: space-around;
  align-items: center;
  border-top: 1px solid #c6c6c6;
  padding-top: 1rem;
`;

const Price = styled.div`
  display: flex;
  align-items: center;
  cursor: pointer;
`;

const PriceIconWrapper = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  width: 2rem;
  height: 2rem;
  margin-right: 0.5rem;
  border-radius: 50%;
`;

const MessageIconWrapper = styled(PriceIconWrapper)`
  background-color: #c6d700;
`;

const CallIconWrapper = styled(PriceIconWrapper)`
  background-color: #00bd1e;
`;

const LocationIconWrapper = styled(PriceIconWrapper)`
  background-color: #ffb800;
`;

const PriceIcon = styled.img`
  width: 1.2rem;
  height: 1.2rem;
`;

const PriceText = styled.span`
  font-size: 1.2rem;
`;

const customStyles = {
  content: {
    top: '50%',
    left: '50%',
    right: 'auto',
    bottom: 'auto',
    marginRight: '-50%',
    transform: 'translate(-50%, -50%)',
    width: '500px',
    padding: '20px',
    textAlign: 'center',
  },
};

const Button = styled.button`
  cursor: pointer;
  background-color: #007bff;
  color: #fff;
  padding: 0.75rem 1.5rem;
  margin-top: 0.75rem;
  border: none;
  border-radius: 12px;
  &:hover {
    background-color: #0056b3;
  }
`;

const consultingData = [
  {
    id: 'supportCenter',
    imgSrc: Consul1Image,
    title: '',
    type: '',
    description:
      '# # #         ',
    prices: {
      message: '',
      call: '',
      site: '',
    },
    availableForCall: true,
    availableForLocation: true,
  },
  {
    id: 'kimYoungMi',
    imgSrc: Consul4Image,
    title: '',
    type: '',
    description:
      '# # # #      .  12~18  ',
    prices: {
      message: '30,000',
      call: '30,000',
      site: '',
    },
    availableForCall: true,
    availableForLocation: false,
  },
  {
    id: 'ChoYeunjeong',
    imgSrc: Consul5Image,
    title: '',
    type: '',
    description:
      '# # # # #   .  12~18  ',
    prices: {
      message: '30,000',
      call: '30,000',
      site: '',
    },
    availableForCall: true,
    availableForLocation: false,
  },
  {
    id: 'ParkHyunWoo',
    imgSrc: Consul6Image,
    title: '',
    type: '',
    description:
      '# # # # #    .  9~12  ',
    prices: {
      message: '30,000',
      call: '30,000',
      site: '',
    },
    availableForCall: true,
    availableForLocation: false,
  },
];

const Consulting = () => {
  const [loginPromptIsOpen, setLoginPromptIsOpen] = useState(false);
  const [filters, setFilters] = useState({
    free: false,
    premium: false,
    searchTerm: '',
    phoneConsultation: false,
    inPersonConsultation: false,
  });

  const navigate = useNavigate();

  const handleLoginConfirm = () => {
    setLoginPromptIsOpen(false);
    window.location.href = '/login';
  };

  const handleFilterChange = (newFilters) => {
    setFilters(newFilters);
  };

  const handlePriceClick = () => {
    if (!localStorage.getItem('access_token')) {
      setLoginPromptIsOpen(true);
    } else {
      navigate('/consulting/application');
    }
  };

  const filteredData = consultingData.filter((consultant) => {
    const matchesPhoneConsultation = filters.phoneConsultation ? consultant.availableForCall : true;

    const matchesInPersonConsultation = filters.inPersonConsultation
      ? consultant.id === 'supportCenter'
      : true;

    const matchesFreeAndPremium =
      (filters.free && filters.premium) || 
      (!filters.free && !filters.premium) ||
      (filters.free && consultant.prices.message === '') || 
      (filters.premium && consultant.prices.message !== ''); 

    const matchesSearchTerm =
      consultant.title.includes(filters.searchTerm) ||
      consultant.description.includes(filters.searchTerm);

    return (
      matchesPhoneConsultation &&
      matchesInPersonConsultation &&
      matchesFreeAndPremium &&
      matchesSearchTerm
    );
  });

return (
  <ConsultingContainer>
    <SearchSide onFilterChange={handleFilterChange} />
    <ResultSide>
      <ResultHeader>  /   </ResultHeader>
      <ResultBorder />
      <Result>
        {filteredData.map((consultant) => (
          <ResultItem key={consultant.id}>
            <ResultContent>
              <ResultImage src={consultant.imgSrc} alt={consultant.title} />
              <ResultDetails>
                <ResultTitle>{consultant.title}</ResultTitle>
                <Highlight>{consultant.type}</Highlight>
                <ResultDescription>{consultant.description}</ResultDescription>
              </ResultDetails>
            </ResultContent>
            <Prices>
              <Price onClick={handlePriceClick}>
                <MessageIconWrapper>
                  <PriceIcon src={MessageIconImage} alt="Message" /> {/*   */}
                </MessageIconWrapper>
                <PriceText>: {consultant.prices.message}</PriceText>
              </Price>
              <Price onClick={handlePriceClick}>
                <CallIconWrapper>
                  <PriceIcon src={CallIconImage} alt="Call" /> {/*   */}
                </CallIconWrapper>
                <PriceText>: {consultant.prices.call}</PriceText>
              </Price>
              {consultant.availableForLocation && (
                <Price onClick={handlePriceClick}>
                  <LocationIconWrapper>
                    <PriceIcon src={LocationIconImage} alt="Location" /> {/*   */}
                  </LocationIconWrapper>
                  <PriceText>: {consultant.prices.site}</PriceText>
                </Price>
              )}
            </Prices>
          </ResultItem>
        ))}
      </Result>
    </ResultSide>

    {loginPromptIsOpen && (
      <Modal
        isOpen={loginPromptIsOpen}
        onRequestClose={() => setLoginPromptIsOpen(false)}
        contentLabel=" "
        ariaHideApp={false}
        style={customStyles}
      >
        <h2> </h2>
        <p>    .   ?</p>
        <Button onClick={handleLoginConfirm}> </Button>
      </Modal>
    )}
  </ConsultingContainer>
);
};

export default Consulting;
